import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { UserComponent } from './user.component';
import { UserSidebarComponent } from './components/user-sidebar/user-sidebar.component';

import { AuthGuard } from '.././auth.guard';

export const routes: Routes = [

	{ path: '', component: UserComponent,
	
		children: [
			{ path: '', redirectTo: 'dashboard', pathMatch: 'full'},
			{ path: 'dashboard', loadChildren: () => import('./user-dashboard/user-dashboard.module').then(m => m.UserDashboardModule) },
			{ path: 'myprofile', loadChildren: () => import('./my-profile/my-profile.module').then(m => m.MyProfileModule) },
			{ path: 'inbox', loadChildren: () => import('./user-inbox/user-inbox.module').then(m => m.UserInboxModule) },
			{ path: 'payment', loadChildren: () => import('./user-payment/user-payment.module').then(m => m.UserPaymentModule) },
			{ path: 'notify-admin', loadChildren: () => import('./notify-admin/notify-admin.module').then(m => m.NotifyAdminModule) },
			{ path: 'social-chat', loadChildren: () => import('./social-chat/social-chat.module').then(m => m.SocialChatModule) },
			{ path: 'alert-security', loadChildren: () => import('./alert-security/alert-security.module').then(m => m.AlertSecurityModule) },
			{ path: 'servicedesk', loadChildren: () => import('./servicedesk/servicedesk.module').then(m => m.ServicedeskModule) },
			{ path: 'moveinout-tracker', loadChildren: () => import('./user-moveinout-tracker/user-moveinout-tracker.module').then(m => m.UserMoveinoutTrackerModule) },
			{ path: 'visitor', loadChildren: () => import('./user-visitor/user-visitor.module').then(m => m.UserVisitorModule) },
			{ path: 'facility-booking', loadChildren: () => import('./facility-booking/facility-booking.module').then(m => m.FacilityBookingModule) },
			{ path: 'documents', loadChildren: () => import('./user-documents/user-documents.module').then(m => m.UserDocumentsModule) }
		]

	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class UserRoutingModule { }
