import { Component, OnInit, Injector, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ApartmentService } from '../../.././api/services/apartment.service';
import { UserService } from '../../.././api/services/user.service';
import { SharedService } from '../../../shared/services/shared.service';
import { ModalService } from '../../../shared/services/modal.service';
import { ConstantsService } from '../../../shared/services/constants.service';
import { CookieService } from 'ngx-cookie-service';

import { MalihuScrollbarService } from 'ngx-malihu-scrollbar';

@Component({
  selector: 'app-user-sidebar',
  templateUrl: './user-sidebar.component.html',
  styleUrls: ['./user-sidebar.component.scss']
})
export class UserSidebarComponent implements OnInit {

  activeNav:string = "";

  isSideBarMini:boolean = false;

  isApartmentSelected:boolean = false;

  apartmentName:string = "";

  modalService:any;

  user:any;
  userLoggedIn:boolean = false;

  loginType:any;

  constructor(
  private injector: Injector,
  private router: Router,
  private route: ActivatedRoute,
  private apartmentService:ApartmentService,
  private userService:UserService,
  private cookieService:CookieService,
  private constantsService:ConstantsService,
  private sharedService:SharedService,
  private mScrollbarService: MalihuScrollbarService
  ) { 

    this.modalService = this.injector.get(ModalService);

  	router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
      		let url = this.router.url.split('user/');
        	this.activeNav = url[url.length-1];
          if(window.innerWidth <= 991){
            this.isSideBarMini = false;
            this.sharedService.setSideBarMini(this.isSideBarMini);
          }
          
      }
    });

  }

  toggleNav(name){
  	if(this.activeNav != name){
  		this.activeNav = name;
  	}
  	else {
  		this.activeNav = "";
  	}
  }

  isNavActive(name){
  	return this.activeNav == name ? 'nav-active' : '';
  }

  showMiniSideBar(){
    return this.isSideBarMini ? 'mini' : '';
  }

  showApartments(){
    this.modalService.showApartmentModal();
  }

  showUserInfo(){
    this.modalService.showUserInfoModal(this.user);
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if(window.innerWidth <= 991){
      this.mScrollbarService.destroy('#leftMenuScroller');
    }
    else {
      this.mScrollbarService.initScrollbar('#leftMenuScroller', {});
    }
  }

  ngOnInit() {

    this.user = {};

    var userId = parseInt(this.cookieService.get('userId'));

    this.userService.getUserById(userId).subscribe((res:any) => {
      
      this.user = res[0];

      this.userService.getRolesByUserId(userId).subscribe((data:any) => {

        this.user.role = data[0].roleName;
        this.sharedService.setUserDetails(this.user);
          
        setTimeout(() => {
          this.sharedService.isUserLogged(true);
        }, 1000);

        

      },
      error => {
        console.log(error);
      }
      );

    },
    error => {
      console.log(error);
    });

    this.apartmentService.getAllApartmentsByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {
      
      this.apartmentName = res[0].apartmentName;
      this.cookieService.set('apartmentId', res[0].apartmentId);
      this.isApartmentSelected = true;
    },
    error => {
      console.log(error);
    }
    );

    this.sharedService.sidebarminicast.subscribe(isSideBarMini => this.isSideBarMini = isSideBarMini);
    
    this.sharedService.userlogin.subscribe( userLoggedIn => this.userLoggedIn = userLoggedIn );

  }

  ngAfterViewInit() {

    if(window.innerWidth <= 991){
      this.mScrollbarService.destroy('#leftMenuScroller');
    }
    else {
      this.mScrollbarService.initScrollbar('#leftMenuScroller', {});
    }

  }

}
